        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%; /* 14px */
        text-transform: uppercase;
    }
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: var(--tc-theme-primary);
        z-index: -1;
    }
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .avatar-1 {
        width: 54px;
        height: 54px;
    }
    .avatar-2 {
        width: 54px;
        height: 54px;
        border: 2px solid var(--tc-system-black);
        margin-left: -20px;
    }
}
