.fintech-app-home-section-6 {
    .card-team {
        &:hover {
            .team-overlay {
                opacity: 1;
                &::before {
                    opacity: 0.75;
                    transform: translateX(0%);
                }
                box-shadow: 0px 40px 80px 0px rgba(177, 227, 70, 0.2);
            }
        }
        .team-overlay {
            content: '';
            position: absolute;
            transition: all 0.3s linear;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            opacity: 0;
            &::before {
                content: '';
                position: absolute;
                transition: all 0.3s linear;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background: var(--tc-theme-primary);
                opacity: 0;
                transform: translateX(-50%);
                border-radius: 10px;
                box-shadow: 0px 40px 80px 0px rgba(177, 227, 70, 0.2);
            }
            .icon {
                position: relative;
                z-index: 3;
                width: 34px;
                height: 34px;
                border-radius: 34px;
                svg {
                    height: 14px;
                    width: 14px;
                    fill: var(--tc-system-black);
                    opacity: 0.5;
                }
                &:hover {
                    background: #292929;
                    svg {
                        fill: var(--tc-system-white);
                        opacity: 1;
                        transition-duration: 0.3s;
                    }
                }
            }
        }
    }
}
.fintech-app-home-section-8 {
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #222;
        z-index: -1;
    }
    .accordion {
        .card {
            transition: all 0.3s ease-in-out;
            .card-header {
                a {
                    .arrow {
                        &::before {
                            transition: 0.3s;
                            transform: rotate(0deg);
                            font-family: bootstrap-icons;
                            content: '\F2EA';
                            color: var(--tc-theme-primary);
                            font-size: 20px;
                        }
                    }
                    border-bottom: 1px solid transparent;
                    &.collapsed {
                        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
                        .arrow {
                            background-color: transparent;
                            &::before {
                                transition: 0.3s;
                                color: var(--tc-system-white);
                                transform: rotate(180deg);
                                content: '\F4FE';
                            }
                        }
                    }
                }
            }
            &:first-child {
                .card-header {
                    a {
                        border-top: 1px solid rgba(255, 255, 255, 0.2);
                    }
                }
            }

            .collapse {
                &.show {
                    border-bottom: 1px solid var(--tc-theme-primary);
                }
            }
        }
    }
}
.section-footer-18 {
    background: #222;
    .icon {
        svg {
            height: 14px;
            width: 14px;
            fill: var(--tc-system-white);
            opacity: 0.5;
        }
        &:hover {
            svg {
                fill: var(--tc-system-white);
                opacity: 1;
                transition-duration: 0.3s;
            }
        }
    }
    .input-group {
        .form-control {
            border: 1px dashed var(--tc-theme-primary);
            background-color: var(--tc-system-black);
            color: #fff;
        }
    }
}
// about
.fintech-app-about-section-3 {
    .steps {
        cursor: pointer;
        &:hover {
            .icon-shape {
                transition: all 0.3s ease-in-out;
                background: var(--tc-theme-primary) !important;
                svg {
                    transition: all 0.3s ease-in-out;
                    filter: invert(1);
                }
            }
        }
    }
}
.fintech-app-features-section-3 {
    .card-feature {
        cursor: pointer;
        &:hover {
            background: var(--tc-theme-primary) !important;
            transition: all 0.3s ease-in-out;
            .icon {
                filter: invert(1);
            }
            h6 {
                color: #111;
            }
            p {
                color: rgba(17, 17, 17, 0.8) !important;
            }
        }
    }
}
.pagination_item {
    background: rgba(255, 255, 255, 0.06);
    color: var(--tc-system-white);
    font-weight: 600 !important;
    border-radius: 10px !important;
    svg {
        path {
            stroke: var(--tc-system-white) !important;
            transition: 0.3s ease-in-out;
        }
    }
    &.active {
        background: var(--tc-theme-primary);
        color: var(--tc-system-black) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-black) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:hover {
        background: var(--tc-theme-primary);
        color: var(--tc-system-black) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-black) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
    &:focus {
        box-shadow: none;
        background-color: var(--tc-theme-primary) !important;
        color: var(--tc-system-black) !important;
        transition: 0.3s ease-in-out;
        svg {
            path {
                stroke: var(--tc-system-black) !important;
                transition: 0.3s ease-in-out;
            }
        }
    }
}
